import React from 'react';

const Rating = (props:any) => {
const count = [];
    for (let i = 0; i < props.data; i++) {

    count.push(i);
    }

    return (
        <div className = 'card-raiting'>
            {count.map((elem:any) =>(
                <img id = {elem} src = {require('../../assets/images/star.jpg')}/>
            ))}
        </div>
    );
};

export default Rating
