import React from 'react';
import items from '../../store/dataItems'
import Item from '../../components/itemCard'

const ConsumerAuto = () => {
    return (
        <>
        {items.filter(elem => elem.type === 'consumer').map((itemCards:any) =>(
            <Item data = {itemCards}/>
        ))}
        </>
    )
}

export default ConsumerAuto
